import React from "react";
import { AwesomeButton } from "react-awesome-button";
import "react-awesome-button/dist/themes/theme-c137.css";
import { withNavigationContext } from "react-awesome-slider/dist/navigation";
import Lettering from "../lettering/lettering";
import Background from "../background/background";
import Content from "../content/content";
import Mouse from "../mouse/mouse";
import Section from "../section/section";
import Page from "../page/page";
import "./fullpage.css";

import { FaJs, FaAngular, FaVuejs, FaAws, FaNodeJs, FaReact, FaPython, FaJira} from 'react-icons/fa';
import { SiDjango, SiFlask, SiTensorflow, SiKeras } from "react-icons/si";
export const Home = withNavigationContext(({ fullpage }) => {
  return (
    <Section wrapper={false} backgroundColor="#292c35">
      <Content
        main={
          <Lettering
            title=" ABOUT ME "
            text={[
              "An enthusiastic and highly motivated individual, mastered frontend and backend.",
              "Using tech stacks such as MEAN, and MERN stacks. Javascript, Typescript, React, Node, Angular, Vue, etc.",
              "I’m a U.S. citizen From Myrtle Beach, SC. Studied at Coastal Carolina University along with a certification from Bottega University for Full-Stack Development.",
              "I currently reside in Atlanta, GA as a Freelance Full-Stack Engineer. Open to continuous long-term contract roles. As well as direct-hire roles. Check me out. "
            ]}
          />
        }
        action={
          <div className="button">
            <AwesomeButton
              size="large"
              onPress={() => {
                fullpage.navigate("/page-two");
              }}
            >
              Goto the next page
            </AwesomeButton>
          </div>
        }
      />
    </Section>
  );
});

